@use "variables" as v;

/* Typography */
h1,
h2,
h3 {
  font-family: v.$main-font;
  margin: 0 0 v.$separation;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
  color: v.$primary;

  span {
    color: v.$secondary;
    font-size: 1.8rem;
    display: block;
  }

  &::after {
    content: "";
    display: block;
    width: 10rem;
    height: 10rem;
    margin: 0 auto;
    background-image: url(../img/coffee.svg);
    background-size: 10rem;
  }
}

.white-heading {
  @extend h2;
  color: v.$white;

  span {
    color: v.$white;
  }

  &::after {
    display: none;
  }
}

h3 {
  font-size: 3.2rem;
}
