/* Globals */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background-color: #ebe7de;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Typography */
h1,
h2,
.white-heading,
h3 {
  font-family: "Raleway", sans-serif;
  margin: 0 0 5rem;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2, .white-heading {
  font-size: 3.6rem;
  color: #5e311e;
}
h2 span, .white-heading span {
  color: #c39c63;
  font-size: 1.8rem;
  display: block;
}
h2::after, .white-heading::after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  margin: 0 auto;
  background-image: url(../img/coffee.svg);
  background-size: 10rem;
}

.white-heading {
  color: #ffffff;
}
.white-heading span {
  color: #ffffff;
}
.white-heading::after {
  display: none;
}

h3 {
  font-size: 3.2rem;
}

/* Header */
.header {
  padding: 5rem 0;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header {
    min-height: 70rem;
  }
}
.header::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header .header-content {
  position: relative;
}
.header .header-content h1 {
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header .header-content h1 {
    margin-top: 10rem;
  }
}

/* Header of each page */
.home-header {
  background-image: url(../img/home_header.jpg);
}

.about-us-header {
  background-image: url(../img/about_us_header.jpg);
}

.process-header {
  background-image: url(../img/process_header.jpg);
}

.menu-header {
  background-image: url(../img/menu_header.jpg);
}

.gallery-header {
  background-image: url(../img/gallery_header.jpg);
}

.contact-header {
  background-image: url(../img/contact_header.jpg);
}

/* Bar */
.bar {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.bar .logo {
  width: 50%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .bar .logo {
    width: 20rem;
    margin: 0;
  }
}

/* Navigation */
.main-nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .main-nav {
    flex-direction: row;
    margin-top: 0;
  }
}
.main-nav a {
  text-align: center;
  color: #ffffff;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
}
.main-nav a:hover {
  color: #c39c63;
}
.main-nav a.active {
  color: #c39c63;
}

/* Footer */
.footer {
  background-color: #5e311e;
}
.footer .footer-content {
  padding: 5rem;
}
@media (min-width: 768px) {
  .footer .footer-content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer .copyright {
  text-align: center;
  margin: 0;
  padding: 2rem;
  color: #ffffff;
}

.footer-box {
  font-family: "Bona Nova", serif;
  text-align: center;
  margin-bottom: 5rem;
}
.footer-box:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .footer-box {
    margin-bottom: 0;
  }
}
.footer-box h3 {
  font-family: "Bona Nova", serif;
  margin: 0 0 1rem;
  font-size: 2.4rem;
  color: #c39c63;
}
.footer-box p,
.footer-box a {
  font-size: 2rem;
  margin: 0;
}

/* Main Content */
.main-content {
  background-color: #ffffff;
  padding: 5rem 2rem;
}
@media (min-width: 768px) {
  .main-content {
    padding: 10rem;
    margin-top: -10rem;
    position: relative;
  }
}

/* Icons */
.icons {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .icons .icons-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icons .icons-list li img {
  margin: 0 auto;
  width: 12rem;
}
.icons .icons-list li p {
  font-family: "Bona Nova", serif;
  font-size: 3.2rem;
  color: #5e311e;
}

/* Gallery */
.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 1024px) {
  .gallery {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.gallery .image img {
  height: 40rem;
  width: 40rem;
  object-fit: cover;
}
@media (min-width: 1024px) {
  .gallery .image:nth-child(1) {
    grid-row: 1/3;
  }
  .gallery .image:nth-child(1) img {
    height: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(2) {
    grid-column: 2/4;
  }
  .gallery .image:nth-child(2) img {
    width: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(4) {
    grid-row: 2/4;
  }
  .gallery .image:nth-child(4) img {
    height: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(5) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(5) img {
    width: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(6) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(6) img {
    width: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(7) {
    grid-row: 3/5;
  }
  .gallery .image:nth-child(7) img {
    height: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(8) {
    grid-column: 2/5;
  }
  .gallery .image:nth-child(8) img {
    width: 124rem;
    object-position: center;
  }
  .gallery .image:nth-child(9) {
    grid-column: 1/3;
  }
  .gallery .image:nth-child(9) img {
    width: 82rem;
    object-position: center;
  }
  .gallery .image:nth-child(10) {
    grid-column: 3/5;
  }
  .gallery .image:nth-child(10) img {
    width: 82rem;
    object-position: center;
  }
}

/* Our Menu */
.our-menu {
  background-image: url(../img/upper_menu.svg), url(../img/lower_menu.svg), linear-gradient(to top, #5e311e, #5e311e);
  background-repeat: no-repeat;
  background-position: center 5rem, center 92%, 100%;
  background-size: 200%;
  padding: 15rem 2rem 20rem;
}
@media (min-width: 768px) {
  .our-menu {
    padding: 15rem 0 20rem;
    background-size: 100%;
  }
}
@media (min-width: 1024px) {
  .our-menu {
    background-size: 50%, 50%, 100%;
  }
}

@media (min-width: 768px) {
  .menu-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.menu h3 {
  color: #ffffff;
}
.menu li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
.menu li p {
  color: #ffffff;
  font-size: 2.2rem;
  line-height: 1.5;
  margin: 0 0 2rem;
}
.menu li p span {
  display: block;
  color: #c39c63;
}
.menu li .price {
  font-size: 4rem;
  font-weight: 700;
}

/* Our Testimonials */
.our-testimonials {
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .our-testimonials {
    padding: 10rem;
  }
}
.our-testimonials .testimonial {
  background-color: #ebe7de;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
}
.our-testimonials .testimonial .author {
  text-align: right;
  font-size: 2.4rem;
  color: #5e311e;
  font-family: "Bona Nova", serif;
}

/* Subpage: About Us */
@media (min-width: 768px) {
  .about-us-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.about-us-content .image {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .about-us-content .image {
    margin: 0 2rem 0 0;
  }
}
.about-us-content .image img {
  transform: rotate(-6deg);
  border: 3rem solid #ffffff;
  box-shadow: 9px 10px 5px -7px rgba(0, 0, 0, 0.35);
  margin: 0 auto;
}
.about-us-content .text blockquote {
  margin: 0;
  padding: 1rem;
  border-left: 0.5rem solid #5e311e;
  font-size: 2rem;
}

/* Subpage: Process */
.coffee-process {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .coffee-process {
    display: flex;
    gap: 5rem;
    align-items: center;
  }
}
.coffee-process:last-of-type {
  margin-bottom: 0;
}
.coffee-process:nth-child(even) .text {
  order: -1;
}
.coffee-process .image,
.coffee-process .text {
  flex: 1;
}
.coffee-process .text {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .coffee-process .text {
    margin-top: 0;
  }
}
.coffee-process .text h3 {
  margin: 0;
  text-align: left;
  color: #5e311e;
}
.coffee-process .text h3::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: #c39c63;
}

/* Subpage: Menu */
.menu-content {
  background-image: url(../img/coffee.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 100%;
  aspect-ratio: 3/6;
}
@media (min-width: 480px) {
  .menu-content {
    aspect-ratio: 3/4;
  }
}
@media (min-width: 768px) {
  .menu-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    aspect-ratio: 16/6;
    background-size: 40%;
    background-position: bottom right;
  }
}
.menu-content .text p {
  font-size: 2rem;
}

/* Subpage: Contact */
@media (min-width: 768px) {
  .contact-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    background-image: url(../img/coffee_beans.jpg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: 40%;
  }
}

.form .field {
  margin-bottom: 2rem;
}
.form .field:last-of-type {
  margin-bottom: 0;
}
.form .field label {
  text-transform: uppercase;
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
}
.form .field input:not([type=submit]),
.form .field select {
  width: 100%;
  padding: 1rem;
  border: 0.5rem solid #5e311e;
}
.form .field input[type=submit] {
  background-color: #5e311e;
  width: 100%;
  border: none;
  padding: 1rem;
  color: #ffffff;
  font-size: 2.2rem;
  font-weight: 700;
  text-transform: uppercase;
}
.form .field input[type=submit]:hover {
  cursor: pointer;
}

/* Utilities */
.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #5e311e;
}

.bg-white {
  background-color: #ffffff;
}

.margin-negative-10 {
  margin-top: -10rem;
}

.padding-bottom-0 {
  padding-bottom: 0;
}